import $ from 'jquery';

const $counter = $('.counter');

// https://api.kitabisa.com/v2/misc/org-user-count
$.getJSON('https://api.kitabisa.com/v2/misc/org-user-count', function(json, textStatus) {
    const numbers = json.data.toString().split('');
    numbers.forEach(number => {
        $counter.append(`<li>${number}</li>`)
    })
});




