import $ from 'jquery';
import Modernizr from 'modernizr';

if (!Modernizr.promises) {

    const query = Modernizr.mq('(min-width: 768px)');

    if(query){
        const $evens = $('.desc-left-right .desc__item:nth-of-type(even)')

        $evens.each( function(index) {
            const $imgElement = $(this).find('.desc__item-img');
            const $textElement = $(this).find('.desc__item-text');

            $imgElement.detach().insertAfter($textElement)
        });
    }
}



